.Home{
    background: #E5E5E5;
    
   
}

.header{
    display: flex;      
    width: 100%;
    background-color: #5D30DD;
    justify-content: center;

    .top {
        display: flex;
        width: 100%;
        background-color: transparent;
        justify-content: space-between;
        align-items: center;
        padding: 0 50px;
        height: 60px;  
        max-width: 1300px;

        &_logo {
            display: flex;

            img.img-logo {
                width: 150px;
            }
        }

        &_question {
            display: flex;

            h4 {
                color: #ffffff;
                font-size: 12px;
                font-weight: 500;
            }
        }
    }
}


.container-box{
    display: flex;      
    width: 100%;
    justify-content: center;
    

    .adjust-box{

        display: flex;
        padding: 0 20px;
        max-width: 1300px;

    }

    .central-box {
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    .profile {
        display: flex;
        width: 92%;
        height: 250px;
        background: #F7F7F7;
        margin: 30px 30px 3px;
        margin-right: 0;
        box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.1);

        &_picture-box {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
            height: 75%;
            padding-top: 20px;
            width: 25%;
        
            .wraper-pic {
                display: flex;
                width: 80%;
                justify-content: center;

                img.img-profile {
                    width: 130px;
                }
            }

            .profile-name {
                display: flex;
                width: 100%;
                justify-content: center;
                gap: 5px;
                height: 25px;        

                h4 {
                    font-family: 'Roboto', sans-serif;
                    font-size: 14px;
                    font-weight: 900;
                    color: #5D30DD;
                    margin: 0;
                }

                .icon-verified {
                    display: flex;
                    width: 20px;
                    height: 20px;
                }

                .icon-info {
                    display: flex;
                    height: 10px;
                    width: auto;
                }
            }

        }

        &_personal-info {
            display: flex;
            margin-top: 20px;
            flex-direction: column;
            width: 65%;

            .content-info {
                display: flex;
                gap: 5px;

                .title-name {
                    display: flex;

                    h1 {
                        font-size: 35px;
                        color: #4B3A7C;
                        font-weight: 900;
                        margin: 0;
                    }
                }
            }

            .content-job-ubication {
                display: flex;
                gap: 10px;
                width: 100%;
                font-family: 'Roboto', sans-serif;
                font-size: 12px;
                
                h4{
                    opacity: 0.5;
                }

                .job-adress {
                    display: flex;
                    gap: 5px;
                    margin-left: 5px;
                }
                
            }

            .social-media-line {
                display: flex;
                margin: 15px 0;
                height: 25px;

                img{
                    width: 25px;
                }

                .social-icons {
                    display: flex;
                    gap: 20px;

                }
            }


            .personal-description {
                display: flex;
                width: 90%;
                font-size: 12px;
                line-height: 20px;
                font-family: 'Roboto', sans-serif;
            
                span {
                    margin-left: 10px;
                    font-size: 11px;
                    color: #4B3A7C;
                    font-weight: bold;
                }
            }

        }

        &_logo-certified {
            display: flex;
            width: 30%;
            justify-content: center;
        }



    }

    .contact {
        display: flex;
        flex-direction: column;
        width: 40%;
        margin-top: 30px;
        padding-right: 30px;

        .bg-contact-wapp{
            background: #ffffff;
        }

        .contact_wa {
            display: flex;
            flex-direction: column;
            background: #5D30DD;
            justify-content: center;
            width: 100%;
            height: 200px;
            border-radius: 0 0 10px 10px;
            align-items: center;
            padding: 40px;
            box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.1);

            .wa-title {
                display: flex;
                flex-wrap: wrap;
                margin-bottom: 10px;

                h3 {
                    font-size: 28px;
                    font-weight: 900;
                    color: #ffffff;
                }
            }

            .wa-button {
                display: flex;
                width: 100%;

                button {
                    display: flex;
                    width: 100%;
                    height: 45px;
                    justify-content: center;
                    align-items: center;
                    border: transparent;
                    border-radius: 5px;
                    background: #25D366;
                    font-size: 14px;
                    font-weight: 900;
                    color: #ffffff;
                    gap: 10px;

                    .wa-icon {
                        font-size: 20px;
                        
                    }
                }
            }
        }
    }


    .enterprise {
        width: 92%;
        display: flex;
        margin: 0 30px;

        &_brands {
            display: flex;
            width: 100%;
            padding: 0 30px;
            height: 65px;
            justify-content: space-around;
            align-items: center;
            background: #F7F7F7;
            box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.1);

            
            img {
                width: 50%;
            }

            .logo-1 {
                display: flex;
                justify-content: center;
                width: 15%;

                img {
                    width: 80%;
                }
            }

            .logo-2 {
                display: flex;
                justify-content: center;
                width: 16%;
            }

            .logo-3 {
                display: flex;
                justify-content: center;
                width: 16%;
            }

            .logo-4 {
                display: flex;
                justify-content: center;
                width: 10%;
            }

            .logo-5 {
                display: flex;
                justify-content: center;
                width: 10%;
            }
        }
    }


    .products-services {
        display: flex;
        margin: 30px;
        margin-right: 0;
        padding: 15px 20px;
        width: 92%;
        background: #F7F7F7;
        box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.1);
    
        &_products-box {
            display: flex;
            flex-direction: column;
            width: 65%;
            
            .title {
                display: flex;
                align-items: center;
                font-size: 20px;

                h3 {
                    color: #5D30DD;
                    font-weight: 900;
                }
            }

            .main-banner {
                display: flex;
                padding: 30px 35px;
                flex-direction: column;
                background-image: url(../../assets/images/banner.png);
                background-repeat:no-repeat;
                background-size: cover;
                border-radius: 15px;
                

                .banner-title {
                    display: flex;
                    gap: 20px;
                    align-items: center;

                    h4 {
                        font-size: 16px;
                        font-weight: 900;
                        color: #ffffff;
                        margin: 0;
                    }

                    img {
                        height: 14px;
                    }
                }

                .banner-text {
                    display: flex;
                    width: 60%;
                    line-height: 20px;
                    color: #ffffff;
                    margin: 15px 0px;
                }

                .banner-line-bottom {
                    display: flex;
                    justify-content: space-between;
                    align-items: flex-end;
                    
                }

                button {
                    height: 45px;
                    display: flex;
                    justify-content: space-around;
                    width: 65%;
                    border: transparent;
                    border-radius: 5px;
                    background: #35D8D0;
                    align-items: center;
                    font-size: 14px;
                    font-weight: 900;
                    color: #ffffff;
                    padding: 0 20px;

                    img {
                        height: 18px;
                    }
                }
            }
            .corner-arrow {
                display: flex;

                img {
                    height: 24px;
                }
            }


            .shoping-time {
                margin: 10px 20px 0;
                width: 50%;

                h4 {
                    color: #4B3A7C;
                    font-size: 12px;
                }
            }


            .sponsored-by {
                display: flex;
                margin: 0 20px;
                gap: 12px;
                height: 40px;
                align-items: center;

                h3 {
                    margin: 0;
                    font-size: 16px;
                    font-weight: 900;
                    color: #4B3A7C;
                }

                .sponsor1 {
                    text-align: center;
                    margin-top: 6px;

                    img{
                        width: 40px;
                    }
                }

                .sponsor2 {
                    width: 30px;
                    height: 30px;
                    background-color: #ffffff;
                    text-align: center;
                    border-radius: 50%;
                    box-shadow: 2px 2px 4px rgba(163, 156, 156, 0.56);
                    line-height: 25px;
                }
            }
        }


        .mini-services {
            display: flex;
            flex-direction: column;
            width: 35%;
            height: 100%;
            padding-top: 35px;
        
            .box-services {
                display: flex;
                flex-wrap: wrap;
                height: 80%;
                padding: 0px 15px;
                justify-content: space-between;

                h6 {
                    font-size: 14px;
                    color: #ffffff;
                }

                .services-fam {
                    display: flex;
                    width: 46%;
                    justify-content: center;
                    align-items: center;
                    background-repeat: no-repeat;
                    background-size: contain;
                    background-image: url(/static/media/fam.5b6b7534.png);
                }

                .services-health{
                    display: flex;
                    width: 46%;
                    justify-content: center;
                    align-items: center;
                    background-repeat: no-repeat;
                    background-size: contain;
                    background-image: url(../../assets/images/health.png);
                }

                .services-life{
                    display: flex;
                    width: 46%;
                    justify-content: center;
                    align-items: center;
                    background-repeat: no-repeat;
                    background-size: contain;
                    background-image: url(../../assets/images/life.png);
                }

                .services-travel{
                    display: flex;
                    width: 46%;
                    justify-content: center;
                    align-items: center;
                    background-repeat: no-repeat;
                    background-size: contain;
                    background-image: url(../../assets/images/travel.png);
                } 
            }

            .botton-box {
                display: flex;
                padding: 0 15px;
                margin-top: 10px;

                button {
                    display: flex;
                    width: 100%;
                    justify-content: center;
                    height: 40px;
                    align-items: center;
                    border: transparent;
                    background: #5D30DD;
                    color: #ffffff;
                    box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.1);
                    border-radius: 5px;
                }
            } 
        }
    
    }
}
    
.state-box {
    display: flex;
    justify-content: center;
    height: 170px;
    background: #ffffff;

    .state-box .schedule-call {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 0 25px;
        width: 100%;
        border-radius: 0 0 10px 10px;
        background: #ffffff;
        z-index: 1;
        box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.1);
    
        .content-box {
            display: flex;
            width: 100%;
            justify-content: space-between;

            .content-title {
                display: flex;
                justify-content: center;
                align-items: center;
                margin: 0;
                grid-gap: 5px;
                gap: 5px;

                span {
                    background-color: #6FCF97;
                    height: 10px;
                    width: 10px;
                    border-radius: 50%;
                }

                h2 {
                    font-size: 28px;
                    color: #5D30DD;
                    margin: 0;
                    font-weight: 900;
                }
            }

            .icon-videocall {
                display: flex;
                justify-content: center;
                align-items: center;
            }

            .divider{
                display: flex;
    
                span {
                    border-right: 1px solid #5D30DD;
                }
            }
    
            .icon-phone {
                display: flex;
                justify-content: center;
                align-items: center;
                border-right: none;
            }
        }

        .call-box {
            display: flex;
            justify-content: left;
            align-items: center;
            width: 90%;

            h3 {
                text-decoration-line: underline;
                color: #4B3A7C;
                font-weight: 600;
                font-size: 14px;
            }
        }

    }
}


.contact_mail {
    display: flex;
    flex-direction: column;
    background: #ffffff;
    padding-top: 30px;
    align-items: center;

    .content-mail {
        display: flex;
        justify-content: center;
        width: 90%;
        height: 60px;
        border-bottom: 1px solid #1b1b1b38;
        
        .title-button {
            display: flex;
            justify-content: space-around;
            width: 100%;
        
            .write-mail {
                display: flex;
                
                h3 {
                    font-size: 20px;
                    font-weight: bold;
                    color: #5D30DD;
                }
            }

        }
    }

    .text-mail {
        display: flex;
        width: 90%;
        font-size: 10px;
        padding-top: 20px;
    }
}